@charset "UTF-8";
.u-margin-bottom-lg {
  margin-bottom: 10rem; }

.u-margin-top-sm {
  margin-top: 4rem; }

.u-border-top {
  border-top: 0.1rem solid #777;
  padding-top: 2rem;
  color: #ffffff;
  text-align: left; }

.u-item-margin-right-sm > a:not(:last-child) {
  display: inline-block;
  margin-right: 1.8rem; }

.Contact {
  background-image: linear-gradient(105deg, rgba(255, 255, 255, 0.9) 0%, rgba(255, 255, 255, 0.9) 50%, transparent 50%), url(../../images/sample.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 2px;
  box-shadow: 0 1.5rem 4rem rgba(0, 0, 0, 0.3); }
  .Contact::after {
    content: "";
    clear: both;
    display: table; }

.contact__form {
  padding: 10rem 8rem;
  float: left;
  width: 60rem;
  text-align: left; }
  .contact__form > p {
    left: 0;
    position: relative;
    margin-bottom: 5rem; }

.contact__group:not(:nth-child(4)) {
  width: 100%;
  position: relative;
  margin-bottom: 5rem; }

.contact__group:nth-child(4) {
  width: 100%;
  position: relative;
  margin-bottom: 3rem; }

.contact__name,
.contact__email {
  border-radius: 2px;
  padding: 2.5rem 2.5rem;
  padding-bottom: 2.2rem;
  width: 100%;
  height: 5rem;
  border: none;
  font-size: 2rem;
  border-bottom: 3px solid transparent;
  color: #969696; }
  .contact__name::placeholder,
  .contact__email::placeholder {
    font-size: 2rem;
    color: #9d9d9d; }
  .contact__name:placeholder-shown + label,
  .contact__email:placeholder-shown + label {
    transform: translateY(-4rem);
    opacity: 0; }
  .contact__name:focus:invalid,
  .contact__email:focus:invalid {
    border-bottom: 3px solid #f44336; }
  .contact__name:focus,
  .contact__email:focus {
    outline: none;
    box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.1);
    border-bottom: 3px solid #786ce8; }

.contact__label1,
.contact__label2 {
  transition: all 0.5s ease;
  opacity: 1;
  font-size: 1.5rem;
  font-weight: 700;
  top: 6rem;
  left: 1.5rem;
  word-wrap: none;
  position: absolute; }

.contact__radio1,
.contact__radio2,
.contact__label3,
.contact__label4 {
  cursor: pointer;
  display: inline-block; }

.contact__radio1,
.contact__radio2 {
  position: absolute;
  visibility: hidden; }

.contact__radio1:checked + .contact__label3 span::after {
  opacity: 1; }

.contact__radio2:checked + .contact__label4 span::after {
  opacity: 1; }

.contact__label3,
.contact__label4 {
  padding-left: 3.5rem;
  font-size: 2rem;
  display: inline-block;
  white-space: nowrap;
  position: relative; }

.contact__button {
  content: "";
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  border-radius: 50%;
  border: 0.5rem solid #786ce8;
  height: 3rem;
  width: 3rem; }
  .contact__button::after {
    transition: all 0.2s ease;
    content: "";
    position: absolute;
    border-radius: inherit;
    opacity: 0;
    background-color: #786ce8;
    width: 70%;
    height: 70%;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%; }

.contact__group__radio {
  width: 45%;
  display: inline-block; }
  .contact__group__radio:not(:last-child) {
    margin-right: 5%; }

.submit {
  z-index: 0;
  position: relative;
  cursor: pointer;
  text-align: center;
  padding: 1.5rem 4rem;
  font-size: 2rem;
  border-radius: 3rem;
  display: inline-block;
  color: #ffffff;
  background-color: #786ce8;
  border: none;
  text-transform: uppercase; }
  .submit::before {
    border-radius: inherit;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: -1;
    position: absolute;
    display: block;
    background-color: inherit;
    width: 50%;
    height: 50%;
    content: " ";
    transition: all 0.5s; }
  .submit:hover::before {
    transform: translate(-50%, -50%) scale(3);
    opacity: 0; }
  .submit::after {
    content: " →";
    color: inherit; }
  .submit:focus {
    outline: none; }

@media screen and (max-width: 900px) {
  .Contact {
    background-image: linear-gradient(105deg, rgba(255, 255, 255, 0.9) 50%, rgba(255, 255, 255, 0.9) 100%), url(../../images/sample.jpg); }
  .contact__form {
    padding-left: 6rem;
    padding-right: 6rem;
    width: 100%; }
  .contact__group__radio {
    width: 50%;
    display: block; }
    .contact__group__radio:not(:last-child) {
      margin-right: 0;
      margin-bottom: 1.5rem; }
  .contact__group:nth-child(4) {
    margin-bottom: 4rem; } }
